import styled from 'styled-components';

export const ClimateStyle = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 160px;

    span{
        font-size: 16px;
        color: #222222;
        font-weight: 400;
        font-family: 'Mark Pro';

        @media (max-width: 400px) {
            font-size: 14px;
        }

        @media (max-width: 320px) {
            font-size: 12px;
        }
    }

    div {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        padding: 0 5px;
        margin-top: 5px;
    
        img {
            width: 60px;
            height: 60px;
            margin-bottom: 5px;

            @media (max-width: 320px) {
                width: 50px;
                height: 50px;
            }
        }

        h1{
            font-family: 'Mark Pro';
            font-weight: bold;
            font-size: 48px;
            line-height: 0;
            color: #222222;
            letter-spacing: normal;
        }

        @media (max-width: 400px) {
            h1 {
                font-size: 40px;
            }
        }

        @media (max-width: 320px) {
            h1 {
                font-size: 30px;
            }
        }
    }
`;